html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fixed {
  overflow: hidden;
}

.body-isHeaderFixed {
  padding-top: 13rem;
}

p {
  font-size: 2rem;
  color: #000;
  line-height: 1.5;
}

.box {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  width: 100%;
  height: auto;
  border-radius: 1rem;
}

.header {
  background-color: #19c9c0;
}
.header h1 {
  color: #fff;
  text-align: center;
  margin: 0;
}
.header__content {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .header-fixed {
    position: fixed;
    width: 100%;
    box-shadow: 0.3rem 0.3rem 0.3rem #666;
    left: 0;
    top: 0;
  }
}

.nav {
  margin: 4rem 0;
  gap: 1rem;
}
@media (min-width: 768px) {
  .nav {
    margin: 0;
    display: flex;
    gap: 2rem;
  }
}
.nav a {
  display: block;
  text-align: center;
  color: #fff;
  font-size: 2.4rem;
  transition: background-color 0.3s, color 0.3s;
  margin-bottom: 1.5rem;
}
.nav a:hover {
  color: #fef46d;
}
@media (min-width: 768px) {
  .nav a {
    margin: 0;
  }
}

.video {
  position: relative;
  width: 100%;
  height: 70rem;
}
.video .video__overlay {
  background: rgb(165, 3, 255);
  background: linear-gradient(270deg, rgba(165, 3, 255, 0.5) 0%, rgba(255, 0, 110, 0.3) 100%);
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 10;
}
.video .video__content {
  width: 100%;
  height: 100%;
  position: absolute;
}
.video .video__content video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  position: absolute;
}

.overlay__text {
  text-align: center;
}
.overlay__text h2 {
  color: #fff;
  font-size: 7rem;
  margin: 0;
}
.overlay__text p {
  background-color: #f53756;
  color: #fff;
  display: inline-block;
  padding: 1rem 2rem;
  font-weight: bold;
  border-radius: 1rem;
  font-size: 2.4rem;
}

.about-festival {
  padding: 5rem 0;
}
.about-festival .img {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .about-festival .img {
    margin-bottom: 0;
  }
}
.about-festival .img img {
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .about-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.about-festival__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.about-festival__content h2 {
  margin-bottom: 2rem;
}
.about-festival__content .date {
  color: #f53756;
  font-weight: bold;
}
.lineup {
  background-color: #f53756;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 5rem;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #fff;
  margin: 0;
}
.lineup .day {
  text-transform: uppercase;
  font-weight: bold;
}

.escenarios-box {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
@media (min-width: 768px) {
  .escenarios-box {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenarios-box .escenario {
  flex-basis: 100%;
  padding: 5rem 0;
  border-radius: 1rem;
}
.escenarios-box .escenario .escenario__name {
  font-weight: bold;
  color: #000;
}
.escenarios-box .escenario .escenario__name::after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto;
}
.escenarios-box .bg-yellow {
  background-color: #fef46d;
}
.escenarios-box .bg-yellow li:nth-child(odd) {
  background-color: #d1c201;
}
.escenarios-box .bg-green {
  background-color: #19c9c0;
}
.escenarios-box .bg-green li:nth-child(odd) {
  background-color: #11857f;
}

.rock .escenario__name::after {
  background-image: url("../img/icono_rock.png");
}

.edm .escenario__name::after {
  background-image: url("../img/icono_edm.png");
}

.calendary {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendary li {
  padding: 2.5rem 5rem;
  font-weight: bold;
}
.calendary li:last-of-type {
  padding-top: 2.5rem;
  padding-bottom: 1rem;
}
.calendary li span {
  font-weight: 400;
}

.gallery {
  padding: 5rem 0;
}
.gallery h3 {
  text-align: center;
}
.gallery .gallery__images {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .gallery .gallery__images {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .gallery .gallery__images {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .gallery .gallery__images {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}
.gallery .gallery__images picture {
  cursor: pointer;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.508);
  position: fixed;
  z-index: 1000;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.overlay picture {
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: content-box;
}
@media (min-width: 480px) {
  .overlay picture {
    width: 90%;
  }
}
@media (min-width: 768px) {
  .overlay picture {
    width: 70rem;
  }
}
@media (min-width: 992px) {
  .overlay picture {
    width: 75rem;
  }
}
.overlay .btn-close-overlay {
  background-color: #fff;
  position: absolute;
  bottom: 5rem;
  left: 0;
  right: 0;
  margin: auto;
  width: 6rem;
  height: 6rem;
  text-align: center;
  padding: 2rem;
  font-size: 2rem;
  font-weight: bold;
  border-radius: 50%;
  cursor: pointer;
}

.ticket-section {
  padding: 5rem 0;
  background-color: #19c9c0;
}
.ticket-section h3 {
  text-align: center;
  color: #fff;
  margin: 0;
}
@media (min-width: 768px) {
  .ticket-section .tickets-box {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pass__name {
  text-align: center;
  font-weight: bold;
  font-size: 2.4rem;
  color: #fff;
}
.pass__header p, .pass__footer p {
  text-align: center;
  color: #fff;
  margin: 0;
}
.pass__header {
  background-color: #f53756;
}
.pass__header p {
  text-transform: uppercase;
  font-size: 2.5rem;
  font-weight: bold;
}
.pass__body {
  background-color: #fff;
}
.pass__body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pass__body ul li {
  background-color: #fff;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pass__footer {
  background-color: #752f97;
}
.pass__footer p {
  font-size: 2.4rem;
}

.footer {
  display: flex;
  text-align: center;
  justify-content: center;
  padding: 1rem 2rem;
  background-color: #f53756;
}
.footer p {
  font-size: 1.5rem;
  color: #fff;
  font-weight: bold;
}